h2,h3 {
    margin: 1rem 1.2rem 1rem 1.4rem;
  }
  
  body * {
   font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
   color: #212529;
  }
  
  
  header {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-content: center;
   border-bottom: #5795b9 solid;
   
  }
  
  .header-title{
    color: #005b81;
  }
  
  
  .hero{
    position: relative;
  }
  
  .hero-photo {
    width: 100%;
    max-height: 90vh;
  }
  
  .hero-text{
    position: absolute;
    top: 40px;
    left: 20px;
    color: rgb(255, 255, 255);
    padding-left: 20px;
    padding-right: 20px;
    font-size:xx-large ;
  }
  
  
  .panel {
   border: 1px solid #005b81;
   border-radius: 4px;
   box-shadow: rgb(184, 196, 194) 0px 4px 10px -4px;
   box-sizing: border-box;
  
   padding: 1rem 1.5rem;
   margin: 1rem 1.2rem 1rem 1.2rem;
  }
  
  #login {
   background: white;
  }
  
  #read, #results {
   background: #e6f4f9;
  }
  
  #labelStatus[role="alert"] {
   padding-left: 1rem;
   color: purple;
  }
  
  .display {
    color: gray;
  }
  
  .disabled {
   color: gray;
   background-color: gray;
  }
  dl {
  display: grid;
  grid-template-columns:  max-content auto;
  }
  
  dt {
  grid-column-start: 1;
  }
  
  dd {
  grid-column-start: 2;
  }
  
  .card {
      margin-bottom: 15px;
  }
  
  .card-header{
    background-color: rgba(0,0,0,.03);
    padding: 0.75rem 1.25rem;
    border-bottom: 1px solid rgba(0,0,0,.125);
    display: block;
    height: 20px;
  }
  
  .card-body{
    padding: 1.25rem;
  }
  
  .icon-action {
      margin-top: 5px;
      float: right;
      font-size: 80%;
  }
  
  .list-group-item .title {
      margin-top: 5px;
      margin-bottom: 12px;
      font-weight: 600;
  }
  
  .textBody table {
      table-layout: fixed;
      width: 100%;
  
  }
  
  .buttonSpacer {
    padding-left: 20px;
    padding-top: 20px;
  }
  
  .tdLeft {
    text-align: left;
  }
  
  .tdCenter {
    text-align: left;
  }
  
  .message {
    color: red;
    font-size: 1.0rem;
  }
  
  .title{
    float: left;
  }
  
  .title b{
   font-size: medium;
  }
  
  table {
    display: table;
    border-collapse: collapse;
    box-sizing: border-box;
    text-indent: initial;
    border-spacing: 2px;
    border-color: gray;
    border: 1px solid black;
  }
  
  #humanReadableIPS{
    width: 50vw;
    height: 30vh;
  }
  
  
  .collapsible {
    background-color: #dadada;
    color: rgb(0, 0, 0);
    cursor: pointer;
    padding: 18px;
    width: 100%;
    border: none;
    text-align: left;
    outline: none;
    font-size: 15px;
  }
  
  .active, .collapsible:hover {
    background-color: #dadada;
  }
  
  .content {
    padding: 0 18px;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.2s ease-out;
    background-color: #f1f1f1;
  }
  
  #btnCreate {
    width: 200px;
    padding: 6px;
    margin-bottom: 10px;
  }
  
  
  #btnLogin {
    width: 200px;
    padding: 6px;
    margin-bottom: 10px;
    margin-right: 15px;
    color: #f1f1f1;
    background-color: #005b81;
    border: #e6f4f9;
    font-size: large;
  cursor: pointer;
  }
  
  
  #btnLogin:hover {
    background-color: rgb(30, 72, 126);
  }


  .status-category-moderate {
    border-color:#f46d34;
    color:#8f4324;
  }


  .interactions-reference .ddc-status-label {
    float: left;
    margin-top: 8px;
    margin-right: 12px;
    font-weight: 600;
}

.ddc-status-label {
    display: inline-block;
    padding: 0 8px;
    border: 2px solid #f46d34;
    border-radius: 40px;
    line-height: 32px;
    white-space: nowrap;
}

.ddc-reference-list h2, .ddc-reference-list h4, .referenceList h2, .referenceList h4 {
    font-weight: 600;
    font-size: 15px;
}


.interactions-reference {
    margin-top: 24px;
    margin-bottom: 50px;
    border: 1px solid rgb(37, 37, 37);
    padding: 10px;
}